
import styled from 'styled-components';
import { Flex, Div } from  '../../theme/grid';
import { lightblue, yellow } from '../../theme/variables';
import { Link } from 'react-router';

export const NavigationContainer = styled(Flex)`
  position: fixed;
  rigth: 0;
  right: 2em;
  top: 2.0em;
`;

export const NavItem = styled(Link)`
  margin-right: 30px;
  font-size: 1.5em;
  cursor: pointer;
  border-radius: 2px;
  color: ${lightblue};
  position: relative;
  text-decoration: none;
  &:hover {
    color: ${yellow};
    &:after{
      content: ' ';
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background-color: ${lightblue};

      z-index: -1;
      transform: scale(1.3, 1.5);
    }
  }
`;



